@use "global" as *;

.p-news__container {
  padding-top: rem(60);
  padding-bottom: rem(60);
}

.p-news__inner {
  max-width: rem(600);
  @include mq("md") {
    max-width: initial;
  }
}
.p-news__items {
  margin-top: rem(49);
  @include mq("md") {
    display: flex;
    flex-wrap: wrap;
  }
}

.p-news__item {
  background-color: $light-gray;
  display: flex;
  width: 100%;
  @include mq("md") {
    width: calc((100% - rem(46)) / 2);
    margin-right: rem(46);
  }
  padding: rem(15) rem(39) rem(15) rem(10);
}

.p-news__item + .p-news__item {
  margin-top: rem(20);
  @include mq("md") {
    margin-top: initial;
  }
}

.p-news__item:nth-child(even) {
  @include mq("md") {
    margin-right: 0;
  }
}

.p-news__item:nth-child(n + 3) {
  @include mq("md") {
    margin-top: rem(20);
  }
}

.p-news__img-wrapper {
  flex: 0 1 18.0701754%;
  position: relative;
  width: 100%;
}

.p-news__img-wrapper::before {
  content: "";
  display: block;
  padding-top: 97.0873786%;
}

.p-news__img {
  // width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

.p-news__body {
  flex: 1;
  margin-left: 5.2631579%;
}

.p-news__body > *:last-child {
  margin-bottom: 0;
}
.p-news__date {
  font-size: rem(14);
}
.p-news__text {
  margin-top: rem(10);
}

.p-news__items-wrapper {
  display: none;
}

.p-news__items-wrapper.show {
  display: block;
}
